.grid-container {
  display: grid;
  grid-template-columns: [page-start] 1fr [col-end1 col-start2] 3fr [col-end2 col-start3] 1fr [page-end];
  grid-gap: 1px;
  background-color: black;
  padding: 1px; }
  .grid-container > div {
    background-color: white;
    text-align: center;
    padding: 20px 0;
    font-size: 3rem;
    overflow: scroll; }
  .grid-container .header {
    grid-row-start: 1;
    grid-column: 1 / -1; }
  .grid-container .item1 {
    grid-row-start: 2;
    grid-row-end: 5; }
  .grid-container .item2 {
    grid-row: 2 / span 3;
    grid-column: col-start2 / col-end2;
    height: calc(100vh - 6rem); }
    .grid-container .item2 #transcription {
      scrollbar-width: none;
      padding: 1rem;
      font-family: "Courier New", Courier, monospace;
      font-size: 1rem;
      text-align: left; }
      .grid-container .item2 #transcription p {
        margin-bottom: 1rem;
        line-height: 1.3rem; }
  .grid-container .item7 {
    grid-column: page-start / col-end2; }

