.heading--5, .heading--4, .heading--3, .heading--2, .heading--1 {
  font-weight: 500%;
  text-transform: uppercase; }

.footer {
  grid-column: 1 / -1; }
  .footer .music {
    display: flex-box; }
    .footer .music svg {
      width: 2rem;
      height: 2rem; }

body {
  background-color: #fefefe;
  color: #181818;
  font-size: 62.5%;
  font-family: sans-serif; }

.wrapper {
  margin: 0 auto; }
